import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import PostIndex2 from "../components/postIndex2/postIndex2";
import foto1 from "../images/download.svg";

const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B3"
                meta="Toda la información que necesitas sobre la vitamina B2. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B2?"
                description="Toda la información que necesitas sobre la vitamina B2. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B2?"
                lang="es"
            />

            <h1> Vitamina B3, alimentos, propiedades y beneficios</h1>
            <PostIndex2 linksData={[
                {"url": "#funcion", "text": "¿Qué hace la vitamina B3?"},
                {"url": "#necesidad", "text": "Cantidad necesaria B3"},
                {"url": "#alimentos", "text": "Alimentos"},
                {"url": "#deficiencia", "text": "Deficiencia"},
                {"url": "#efectos", "text": "Efectos adversos"},
                {"url": "#exceso", "text": "Exceso"}
            ]}
            />
            <hr/>
            <p class="introductionText">La vitamina B3 pertenece al <a className="link" href="/vitaminaB">grupo de las
                vitaminas B</a>, es una vitamina soluble en agua que no puede ser almacenada en la grasa corporal. Tiene
                dos componentes activos, el &aacute;cido nicot&iacute;nico (niacina) y la nicotinamida (niacinamida).
                Esta vitamina desempe&ntilde;a un papel en m&aacute;s de 200 reacciones enzim&aacute;ticas.&nbsp;</p>
            <p></p>
            <hr/>
            <p>El cuerpo puede sintetizar la vitamina B3 en peque&ntilde;as cantidades, siempre que tenga suficientes
                reservas de magnesio, vitaminas B6 y B2, y tript&oacute;fano. Este &uacute;ltimo es un amino&aacute;cido
                esencial (parte de una prote&iacute;na), es decir, no es producido por el cuerpo y debe ser obtenido de
                fuentes alimenticias.&nbsp;</p>
            <p></p>
            <p></p>
            <h2 name="funcion">¿Para qué sirve la vitamina B3?</h2>
            <p></p>
            <p>Juega un papel en:&nbsp;</p>
            <p></p>
            <ul>
                <li>La formación de glóbulos rojos</li>
                <li>El transporte de oxígeno a las células</li>
                <li>La circulación de la sangre</li>
                <li>Función del sistema digestivo</li>
                <li>Función del sistema nervioso</li>
                <li>Síntesis de hormonas sexuales</li>
                <li>Producción de neurotransmisores</li>
            </ul>
            <h2 name="necesidad">Necesidad de vitmaina B3</h2>
            <p>Algunas personas necesitan mayores cantidades de niacina, especialmente las que padecen la enfermedad de
                Hartnup (un trastorno hereditario asociado con la absorci&oacute;n deficiente de ciertos
                amino&aacute;cidos), la cirrosis hep&aacute;tica, el s&iacute;ndrome carcinoide y el s&iacute;ndrome de
                mala absorci&oacute;n.</p>

            <p>Las personas que se someten a hemodi&aacute;lisis o di&aacute;lisis peritoneal
                (dos t&eacute;cnicas de purificaci&oacute;n que utilizan un filtro artificial [el dializador] o un
                filtro natural [el peritoneo], respectivamente, cuando los ri&ntilde;ones ya no pueden purificar la
                sangre) o que reciben un tratamiento prolongado para la tuberculosis.</p>

            <p>Tambi&eacute;n es probable que
                necesiten mayores cantidades de vitamina B3, al igual que las mujeres embarazadas con embarazos
                m&uacute;ltiples o que amamantan a m&aacute;s de un ni&ntilde;o.&nbsp;</p>
            <p></p>
            <p></p>
            <h2 name="alimentos">Alimentos que contienen vitamina B3 (niacina):&nbsp;</h2>
            <p>Los alimentos ricos en vitamina B3 (niacina) son la carne, el h&iacute;gado, las aves, el pescado, los
                panes y los productos de panader&iacute;a enriquecidos y de grano entero, as&iacute; como los cereales
                enriquecidos listos para el consumo.&nbsp;</p>
            <p></p>
            <p>Alimentos con vitmaina B3 ordenados de mayor a menor cantidad:</p>

            <ul>
                <li>Salteado o estofado de carne de vacuno, cordero o h&iacute;gado de ternera.</li>
                <li>Pollo asado (carne blanca, con o sin piel)</li>
                <li>Bacalao del Atl&aacute;ntico seco y salado</li>
                <li>Chuleta de ternera</li>
                <li>Salm&oacute;n</li>
                <li>At&uacute;n enlatado</li>
                <li>At&uacute;n de aleta amarilla o pez espada al horno o a la parrilla</li>
                <li>Pierna, lomo o jarrete de ternera salteados o cocidos.</li>
                <li>Esturi&oacute;n ahumado</li>
                <li>Pato dom&eacute;stico a la parrilla (sin piel)</li>
                <li>At&uacute;n rojo al horno, caballa espa&ntilde;ola (rayada), salm&oacute;n</li>
                <li>S&aacute;balo horneado</li>
                <li>Caballa espa&ntilde;ola a la parrilla</li>
                <li>Conejo cocido o asado</li>
                <li>Cacahuetes secos o tostados con aceite</li>
                <li>Cereales para el desayuno, todo de salvado</li>
                <li>Cerdo a la parrilla</li>
                <li>Abadejo a la parrilla</li>
                <li>Ostras del Pac&iacute;fico crudas o al vapor</li>
            </ul>
            <p></p>
            <h2 name="deficiencia">Deficiencia de vitamina B3</h2>
            <p>La deficiencia de vitamina B3 (niacina) es rara en los pa&iacute;ses desarrollados, donde son comunes los
                alimentos ricos en prote&iacute;nas. La deficiencia puede deberse a la falta de vitamina B6, que el
                cuerpo necesita para convertir el tript&oacute;fano en niacina. Los alcoh&oacute;licos tambi&eacute;n
                pueden sufrir de deficiencias de vitamina B3 porque el alcohol inhibe la absorci&oacute;n de esta
                vitamina a nivel intestinal.&nbsp;</p>
            <p></p>
            <h3>Signos de deficiencia de vitamina B3:&nbsp;</h3>
            <p></p>
            <ul>
                <li>Entumecimiento de las manos y los pies</li>
                <li>Dolores de cabeza</li>
                <li>Perdida de apetito</li>
                <li>Hipersensibilidad de la piel cuando se expone al sol</li>
                <li>Inflamación de la boca y la lengua</li>
                <li>Cambios de humor</li>
                <li>Diarrea severa y delirio (deficiencia extrema).</li>
                <li>Eritema (enrojecimiento de la piel)</li>

                <li>Lengua de color rojo brillante</li>
                <li>Depresiones</li>

                <li>Dolor de cabeza</li>
                <li>Fatiga</li>
                <li>Pérdida de memoria.</li>
            </ul>

            <h2 name="efectos">Efectos adversos</h2>
            <p>Las grandes dosis de vitamina B3 deben tomarse con fines terap&eacute;uticos s&oacute;lo bajo
                supervisi&oacute;n m&eacute;dica. Se deben realizar peri&oacute;dicamente an&aacute;lisis de sangre para
                evaluar la funci&oacute;n hep&aacute;tica del paciente, en vista de las numerosas reacciones adversas
                que se han notificado y las numerosas interacciones m&eacute;dicas que son posibles.&nbsp;</p>
            <p></p>
            <h2 name="exceso">Signos de exceso de vitamina B3:&nbsp;</h2>
            <p></p>


            <ul>
                <li>Sensación de calor y rubor en la cara y el cuello</li>
                <li>Nauseas</li>
                <li>Disminución de la tolerancia a la glucosa (posible)</li>
                <li>Problemas de visión</li>
                <li>Erupciones</li>
                <li>Dolor de cabeza</li>
            </ul>


            <h2>Efectos en los medicamentos de la vitamina B3</h2>
            <p></p>
            <p>Las interacciones de los medicamentos con la niacina son comunes. Hable con su farmac&eacute;utico, quien
                podr&aacute; decirle si la niacina puede tener un efecto en los medicamentos que usted toma.</p>
        </Layout>
    )
}

export default Mac
